	module.exports = {"data":{"markdownRemark":{"html":"<h3>üëã everyone,</h3>\n<h1>Meet TinyMerch, our 2017 PH Hackathon Submission.</h1>\n<p>We made TinyMerch with the goal of creating a simple, intuitive, embeddable store that anyone can add to their site in a matter of minutes. With TinyMerch you can select a product, (we currently offer t-shirts üëï &#x26; posters üñºÔ∏è with more products, along with digital files, coming soon!), add a custom design, such as your company‚Äôs logo or a fun illustration, really whatever you‚Äôd like! And just like that, you have a product available in your store! After this it‚Äôs just a matter of adding our script to your site and voil√†! You have a store on your site that people can purchase branded, custom content from.</p>\n<p>TinyMerch is perfect for limited runs, promotions, or to simply add a store to your website for the holidays üéÑ. There‚Äôs no inventory, no monthly fee, no hassle or worry about staying on top of orders and fulfillment. We take care of all of this for you!</p>\n<p><strong>TinyMerch is a worry-free way for someone to get started in e-commerce.</strong></p>\n<h3>So How Does TinyMerch Work?</h3>\n<p>TinyMerch is a lightweight layer built on top of and Printful‚Äôs API and uses Stripe for payment processing. Every time an order is placed we automatically send that request to Printful for fulfillment and shipping. Fulfillment costs are then taken from the total and the remaining profits are sent directly to you via Stripe Connect.</p>\n<p>It is true that all this simplicity comes at a small cost. Your profit margins with us will be lower than if you went to a local printer, placed a large order and then sold them yourselves through a service such as Shopify. If you‚Äôre planning on selling a lot, then maybe that‚Äôs the route you should take, but if that amount of overhead and maintenance scares you, don‚Äôt let keep you from selling your designs.</p>\n<p><strong>You‚Äôre who we made TinyMerch for!</strong></p>\n<h3>See a Demo Store:</h3>\n<p>If you want to see a store in action you can checkout the Demo Store on <a href=\"https://tinymerch.com/demo\">tinymerch.com</a></p>\n<h3>See a Demo Store:</h3>\n<p>Already convinced this is for you?\nYou can <a href=\"https://my.tinymerch.com\">Get Started Now!</a> and have a store on your site selling üíØ original content in a matter of minutes!</p>\n<p>Interested in getting these updates in your inbox?\n(<a href=\"https://www.producthunt.com/upcoming/tiny-merch\">Sign Up for Our Newsletter</a>)</p>\n<p>‚úåÔ∏è Best,\nThe Tiny Team</p>","frontmatter":{"date":"November 30, 2017","path":"/blog/ph-hackathon","title":"2017 PH Hackathon"}}},"pathContext":{"prev":false,"next":{"html":"<h3>üëã everyone,</h3>\n<h1>Donating with TinyMerch</h1>\n<p>This article is coming soon!</p>\n<p>Interested in getting these updates in your inbox?\n(<a href=\"https://www.producthunt.com/upcoming/tiny-merch\">Sign Up for Our Newsletter</a>)</p>\n<p>‚úåÔ∏è Best,\nThe Tiny Team</p>","id":"/Users/aeryk/Sites/Nicer/TinyMerch/tinymerch/website/src/pages/11-29-2017-donations/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-11-29","path":"/blog/donations","tags":["third"],"title":"Giving Back Made Simplier","postTitle":"Giving Back","postGreeting":"üëã everyone,","postPreview":"This article is coming soon!"}}}}

